import Sidebar from "@components/sidebar";
import { isObjEmpty } from "@utils";
import classNames from "classnames";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { FormattedMessage } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import validateOptions from "../../../../../constants/validate";
import { add } from "../store/action";
const SidebarAdd = ({ open, toggleSidebar, disable, setDisable }) => {
  const [discountData, setDiscountData] = useState(null);
  const store = useSelector((state) => state.discounts);
  const DiscountOptions = validateOptions.DiscountOptions;
  const { id } = useParams();
  const dispatch = useDispatch();
  const { register, errors, handleSubmit } = useForm();
  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  const onSubmit = (values) => {
    if (isObjEmpty(errors)) {
      setDisable(true);
      dispatch(
        add({
          product_id: parseInt(id),
          // time: parseInt(values.time),
          ...values,
        })
      );
    }
  };
  return (
    <Sidebar
      size="lg"
      open={open}
      title={<FormattedMessage id="discount" />}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit(onSubmit)}>
        {/* <FormGroup>
          <Label for="service">
            <FormattedMessage id="Service" />
          </Label>

          <Input
            type="select"
            name="service"
            id="service"
            defaultValue={discountData && discountData.status}
            innerRef={register({ required: true })}
          >
            <option value="local">Local</option>
            <option value="cloud">Cloud</option>
          </Input>
        </FormGroup> */}
        {/* <FormGroup>
          <Label for="time ">
            <FormattedMessage id="Extend(month)" />

            <span className="text-danger">*</span>
          </Label>
          <Input
            name="time"
            id="time"
            placeholder=" "
            type="number"
            innerRef={register(DiscountOptions.time)}
            className={classNames({ "is-invalid": errors["time"] })}
            defaultValue={discountData && discountData.time}
          />
          <small className="text-danger">
            {errors?.time && errors.time.message}
          </small>
        </FormGroup> */}
        <div className="mt-1"></div>
        <FormGroup>
          <Label for="discount ">
            <FormattedMessage id="discount" />

            <span className="text-danger">*</span>
          </Label>
          <Input
            name="discount"
            id="discount"
            placeholder=" "
            type="number"
            innerRef={register(DiscountOptions.discount)}
            onBlur={() => {
              let discount = document.getElementById("discount");
              if (discount && discount.value) {
                discount.value = discount.value.trim();
              }
            }}
            className={classNames({ "is-invalid": errors["discount"] })}
            defaultValue={discountData && discountData.last_name}
          />
          <small className="text-danger">
            {errors?.discount && errors.discount.message}
          </small>
        </FormGroup>
        <div style={{ textAlign: "end" }}>
          <Button
            type="submit"
            className="mr-1"
            color="primary"
            disabled={disable}
          >
            <FormattedMessage id="Save" />
          </Button>
          <Button
            type="reset"
            color="secondary"
            outline
            onClick={toggleSidebar}
          >
            <FormattedMessage id="Cancel" />
          </Button>
        </div>
      </Form>
    </Sidebar>
  );
};

export default SidebarAdd;
